/* ***************** */
/* FONTS */
/* ***************** */

@font-face{
	font-family: 'WebSymbolsRegular';
	src: url(../../static/media/websymbolsligaregular.44275c99.eot);
          src: url(../../static/media/websymbolsligaregular.44275c99.eot?#iefix) format('embedded-opentype'),
               url(../../static/media/websymbolsligaregular.0aa7651d.woff) format('woff'),
               url(../../static/media/websymbolsligaregular.8c700cbe.ttf) format('truetype'),
               url(../../static/media/websymbolsligaregular.49bf57a1.svg#WebSymbolsRegular) format('svg');
		font-weight: normal;
		font-style: normal;
