/* ***************** */
/* BASIC */
/* ***************** */

body {
  font-family: 'Risque', cursive;
  font-size: 20px;
  background-color: #66426d;
}

ol, ul {
  padding-left: 30px;
}

.black {
	background-color: #272727;
}

#root {
	height: 100vh;
	min-height: 650px;
	position: relative;
}

.icon {
  font-family: WebSymbolsRegular;
}

.header-game {
	width: 200px;
	margin: auto;
	margin-bottom: 20px;
	padding: 10px;
	border: 3px solid #FFF;
	border-bottom: none;
	border-right: none;
	border-radius: 20px;
}

.header-game h1{
	margin: 0;
	padding-top: 76px;
	padding-left: 96px;
	background: url(../../static/media/logo.35b4e6ed.jpg) no-repeat left top;
	background-size: 100px;
	font-size: 24px;
	letter-spacing: 3px;
	color: #fcba00;
}

.game-board {
	padding: 0 70px;
	padding-top: 10px;
}

.start-new-game {
	display: block;
	margin: auto;
	padding: 5px 10px;
	border: none;
	border-radius: 10px;
	cursor: pointer;
	box-shadow: 4px 4px 4px #272727;
	transition: background-color .2s, color .2s;
}

.start-new-game:hover {
	color: #fcba00;
	box-shadow: 4px 4px 4px black;
	background-color: #272727;
}

.start-new-game:active {
	box-shadow: 1px 1px 1px #272727;
}

.start-new-game:focus {
	outline: none;
}

.wrap-game {
	padding-top: 20px;
}

.score {
	position: absolute;
	bottom: 50px;
	left: 10px;
	margin-bottom: 20px;
}

.score p {
	margin: 0;
	padding-bottom: 10px;
	font-size: 20px;
	color: #FFF;
	text-align: center;
}

/* ***************** */
/* BUTTON SETTINGS */
/* ***************** */

.settings-game {
  width: 70px;
	position: fixed;
	right: -20px;
	top: 10px;
  padding: 5px;
  display: flex;
  flex-direction: column;
}

.settings-game button {
	display: block;
	margin-bottom: 5px;
	border-radius: 5px;
	border: 2px solid #fcba00;
  color: #444;
	color: #66426d;
	transition: margin-right .3s;
	cursor: pointer;
	box-shadow: -4px 4px 4px #272727;
}

.settings-game button:hover {
	margin-right: 10px;
}

.settings-game button:focus {
	outline: none;
}

/* ***************** */
/* FULL SCREEN */
/* ***************** */

.fullscreen,
.nofullscreen {
	position: absolute;
	top: 150px;
	left: 5px;
	height: 40px;
	width: 40px;
	border: none;
	background-color: #FFF;
	color: #66426d;
	border-radius: 50%;
}

.fullscreen:hover,
.nofullscreen:hover {
	color: #fcba00;
}

.fullscreen:focus,
.nofullscreen:focus {
	outline: none;
}

:-webkit-full-screen {
  padding-top: 150px;
}

:-moz-full-screen {
  padding-top: 150px;
}

.full {
	display: none;
}

/* ***************** */
/* BUTTONS MUSIC */
/* ***************** */

.wrap-music {
  width: 70px;
	position: fixed;
  left: -20px;
	top: 10px;
  padding: 5px;
  display: flex;
  flex-direction: column;
}

.wrap-music button {
  display: block;
  margin-bottom: 5px;
  border-radius: 5px;
  border: 2px solid #fcba00;
  color: #444;
	color: #66426d;
  transition: margin-left .3s;
  cursor: pointer;
	box-shadow: 4px 4px 4px #272727;
}

.wrap-music button:hover {
  margin-left: 10px;
}

.wrap-music button:focus {
  outline: none;
}

.settings-game .of-sound {
	display: none;
}

/* ***************** */
/* GAME AREA */
/* ***************** */

.game-area-lists {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
}

.game-area-lists > div {
	position: relative;
	width: 100px;
	height: 100px;
	margin-right: 20px;
	margin-bottom: 20px;
	border-radius: 20px;
	box-shadow: 4px 4px 4px #272727;
	cursor: pointer;
	transition: transform .3s;
}

.game-area-lists > div:hover {
	transform: rotate(10deg);
}

.back {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	border-radius: 20px;
	background: url(../../static/media/back-unicorn.ab4ce1f9.jpg) no-repeat #FFF;
	background-size: cover;
}

.back-hidden {
	-webkit-filter: opacity(0);
	        filter: opacity(0);
	transition: -webkit-filter .5s;
	transition: filter .5s;
	transition: filter .5s, -webkit-filter .5s;
}

.disable {
  -webkit-filter: opacity(.7);
          filter: opacity(.7);
  pointer-events: none;
}

/* ***************** */
/* FOOTER */
/* ***************** */

footer {
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	text-align: center;
	font-size: 16px;
	color: #66426d;
	background: url(../../static/media/footer-fon.e9467ad2.jpg) no-repeat, url(../../static/media/footer-fon-right.0bee1a87.jpg) no-repeat top right #FFF;
	background-size: 110px, 100px;
	padding-top: 5px;
	padding-bottom: 0;
	box-shadow: 0 -4px 4px #272727;
}

footer p {
	margin: 10px 0;
}

footer a {
  color: #66426d;
  text-decoration: none;
}

.rsschool {
  padding: 0 10px;
  width: 70px;
}

.rsschool img {
  max-width: 50px;
}

/* ***************** */
/* MODAL */
/* ***************** */

.wrap-modal,
.wrap-end-modal,
.wrap-statistics-modal,
.wrap-settings-modal {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 999;
	background-color: rgba(0, 0, 0, .7);
	display: none;
}

.show {
	display: block;
}

.modal {
	width: 80%;
	margin: 20px auto;
	height: 90vh;
	border-radius: 20px;
	box-shadow: 4px 4px 4px #000;
	position: relative;
	background: url(../../static/media/modal-about.833a144a.jpg) no-repeat bottom right #FFF;
	background-size: 25%;
}

.wrap-end-modal .modal {
	background: url(../../static/media/modal-end.13d3b014.jpg) no-repeat bottom center #FFF;
	background-size: 30%;
}

.wrap-statistics-modal .modal {
	background: url(../../static/media/modal-statistics.d3b5329f.jpg) no-repeat bottom right #FFF;
	background-size: 30%;
}

.wrap-settings-modal .modal {
	background: url(../../static/media/modal-settings.4c4758b4.jpg) no-repeat 50px bottom #FFF;
	background-size: 20%;
}

.modal button {
	position: absolute;
	right: 20px;
	top: 20px;
	border: none;
	background-color: transparent;
	transition: color .2s;
}

.modal button span {
	color: #66426d;
	cursor: pointer;
}

.modal button span:hover {
	color: #fcba00;
}

.modal button:focus {
	outline: none;
}

.modal button span {
	font-size: 40px;
}

.modal-content {
	padding: 50px;
	font-family: 'Shippori Mincho', serif;
}

.modal-content h2 {
	color: #66426d;
	text-align: center;
}

.modal-content p {
	text-align: justify;
	line-height: 1.7;
}

.wrap-settings-modal .modal-content p {
	text-align: center;
	line-height: 1.7;
}

.modal-content strong {
	color: #66426d;
}

.settings-size {
	text-align: center;
}

.settings-size input,
.settings-size label {
	margin-right: 20px;
}

.settings-apply {
	padding-top: 20px;
}
.settings-bg,
.settings-cards,
.settings-apply {
		text-align: center;
}

.settings-bg button,
.settings-cards button,
.settings-apply button {
	position: static;
	padding: 10px 15px;
	margin-right: 20px;
	border-radius: 10px;
	background-color: #66426d;
	color: #FFF;
	cursor: pointer;
}

.settings-bg button:hover,
.settings-cards button:hover,
.settings-apply button:hover {
	box-shadow: 4px 4px 4px #272727;
	background-color: #fcba00;
}

.hot-keys {
	list-style-type: none;
	padding-left: 0;
}

p.end-steps,
p.end-score,
p.end-description {
	text-align: center;
	margin: 10px 0;
}

p.user-name {
	text-align: center;
}

p.user-name input {
	border: none;
	border-bottom: 1px solid #66426d;
}

p.user-name input:focus {
	outline: none;
}

button.ok {
	margin-left: 10px;
	/* display: inline-block; */

	color: #000;
	background-color: #FFF;
	position: static;
	border: 1px solid #000;
	border-radius: 5px;
	box-shadow: 4px 4px 4px #272727;
	cursor: pointer;
}

button.ok span {
	display: block;
}

.user-name button {
	padding: 5px 10px;
}

button.ok:hover {
	color: darkred;
}

button.ok:focus {
	outline: none;
}

table {
	margin: auto;
}

tbody tr:nth-child(odd) {
	background-color: #DDD;
}

table td {
	width: 100px;
	padding: 5px;
	border-right: 1px solid #272727;
	font-size: 16px;
	text-align: center;
}

table tr td:last-child {
	border-right: none;
}

/* ***************** */
/* MEDIA */
/* ***************** */

@media screen and (max-width: 1150px) {
	.game-area-lists > div {
	}
}

@media screen and (max-width: 1000px) {
	#root {
		height: auto;
		min-height: 90vh;
		padding-bottom: 100px;
	}
}

@media screen and (max-width: 840px) {
	#root {
		height: auto;
		min-height: 90vh;
	}

	.modal-content {
		padding: 30px;
		padding-top: 50px;
		font-size: 16px;
	}

	.modal-content p {
		line-height: 1.3;
	}

	footer {
		padding-bottom: 5px;
	}
}

@media screen and (max-width: 650px) {
	footer {
		background: url(../../static/media/footer-fon.e9467ad2.jpg) no-repeat bottom left, url(../../static/media/footer-fon-right.0bee1a87.jpg) no-repeat bottom right #FFF;
		background-size: 80px, 80px;
	}
}

@media screen and (max-width: 560px) {
	footer {
		background: url(../../static/media/footer-fon.e9467ad2.jpg) no-repeat bottom left, url(../../static/media/footer-fon-right.0bee1a87.jpg) no-repeat bottom right #FFF;
		background-size: 60px, 60px;
	}
}

@media screen and (max-width: 500px) {
	footer {
		background: #FFF;
	}
}

@media screen and (max-width: 450px) {
	.modal-content {
		padding: 20px;
		padding-top: 50px;
		font-size: 14px;
	}

	.modal-content p {
		line-height: 1.3;
	}
}

